.header{
    display:flex;
    justify-content: space-between;
    margin:10px;
    border:1px solid black;
}

.nav-items>ul{
    list-style-type: none;
    display: flex;
}

.nav-items > ul > li {
    padding: 10px;
}

.logo{
    width:100px;
}

.card{
    width:200px;
    border:1px solid black;
    padding:10px;
    margin:10px;
}
.restaurant-list{
    display: flex;
    justify-content: space-between;
    flex-wrap:wrap;
}

.card > img {
    width:100%;
}

